.hukidasi_sankaku {
	position: relative;
}

.hukidasi_sankaku::after {
	position: absolute;
	left: 0;
	right: 0;
	bottom: -32px;
	margin: auto;
	width: 0;
	height: 0;
	border-width: 32px 32px 0 32px; 
	border-style: solid;
	content: '';
}

.hukidasi-xs::after {
	bottom: -8px;
	border-width: 8px 8px 0 8px;
}

.hukidasi-s::after {
	bottom: -8px;
	border-width: 16px 16px 0 16px;
}

.hukidasi-m::after {
	bottom: -8px;
	border-width: 24px 24px 0 24px;
}

.hukidasi-l::after {
	bottom: -8px;
	border-width: 32px 32px 0 32px;
}

.hukidasi-xl::after {
	bottom: -8px;
	border-width: 64px 64px 0 64px;
}

.shadow {
	-webkit-box-shadow: 0 8px 16px 0 rgba(51, 51, 51, .5);
	        box-shadow: 0 8px 16px 0 rgba(51, 51, 51, .5);
	-webkit-transition: ease .3s;
	        transition: ease .3s;
}

.shadow:hover {
	-webkit-box-shadow: 0 8px 32px 8px rgba(51, 51, 51, .3);
	        box-shadow: 0 8px 32px 8px rgba(51, 51, 51, .3);
}

.hover-up:hover {
	-webkit-transition: ease .3s;
	        transition: ease .3s;
	-webkit-transform: translate(0, -8px);
	        transform: translate(0, -8px);
}

.hover-huwahuwa:hover {
	-webkit-animation: huwahuwa ease-in-out	2s infinite;
	        animation: huwahuwa ease-in-out	2s infinite;
}

@-webkit-keyframes huwahuwa {
	0% {
		-webkit-transform: translate(0, 0);
		        transform: translate(0, 0);
	}
	50% {
		-webkit-transform: translate(0, -8px);
		        transform: translate(0, -8px);
	}
	100% {
		-webkit-transform: translate(0, 0);
		        transform: translate(0, 0);
	}
}

@keyframes huwahuwa {
	0% {
		-webkit-transform: translate(0, 0);
		        transform: translate(0, 0);
	}
	50% {
		-webkit-transform: translate(0, -8px);
		        transform: translate(0, -8px);
	}
	100% {
		-webkit-transform: translate(0, 0);
		        transform: translate(0, 0);
	}
}

