.midasi {
	position: relative;
	display: inline-block;
	margin-bottom: 1em; 
	font-family: 'Kosugi Maru';
}

.midasi:before {
	position: absolute;
	left: 50%;
	bottom: -15px;
	display: inline-block;
	width: 60px;
	height: 8px;
	background-color: #72cfc7; 
	content: '';
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
}

.midasi-2 {
	position: relative;
	padding: .4em .5em; 
	border-top: solid 2px #72cfc7;
	border-bottom: solid 2px #72cfc7;
	background: #f4f4f4;
}
.midasi-2:after {
	/*タブ*/
	position: absolute;
	left: 0;
	bottom: 100%;
	padding: 5px 7px 3px;
	border-radius: 5px 5px 0 0;
	background: #72cfc7;
	content: '\f0a7\ Check!';
	color: #ffffff;
	font-size: .7em;
	font-family: 'Font Awesome 5 Free','Quicksand','Avenir','Arial',sans-serif;
	letter-spacing: .05em; 
	line-height: 1;
}

.midasi-3 {
	position: relative;
	border-top: solid 1px #333333;
	border-bottom: solid 1px #333333;
}
.midasi-3:before,
.midasi-3:after {
	position: absolute;
	top: -10px;
	width: 1px;
	height: calc(100% + 20px);
	background-color: #333333; 
	content: '';
}
.midasi-3::before {
	left: 2%;
}
.midasi-3::after {
	right: 2%;
}

