.accordion::-webkit-scrollbar * {
	display: none;
}

.accordion input {
	position: absolute;
	z-index: -1;
	opacity: 0;
}

.accordion label {
	position: relative;
	display: block;
	cursor: pointer;
}

.accordion .accordion-content {
	overflow: hidden; 
	max-height: 0;
	-webkit-transition: max-height .6s;
	        transition: max-height .6s;
}

.accordion input:checked ~ .accordion-content {
	overflow: auto; 
	max-height: 30em;
}

.accordion[class*='cursor'] label {
	padding-left: 1em;
	padding-right: 2em;
}

.accordion[class*='cursor'] label::after {
	position: absolute;
	top: 50%;
	right: .5em;
	content: '\f078';
	font-weight: 900;
	font-family: 'Font Awesome 5 Free';
	-webkit-transition: -webkit-transform .3s;
	        transition: -webkit-transform .3s;
	        transition:         transform .3s;
	        transition:         transform .3s, -webkit-transform .3s;
	-webkit-transform: translateY(-50%) rotate(0deg);
	        transform: translateY(-50%) rotate(0deg);
}

.accordion[class*='cursor'] input:checked ~ label::after {
	-webkit-transform: translateY(-50%) rotate(-180deg);
	        transform: translateY(-50%) rotate(-180deg);
}

.accordion[class*='faq'] label {
	padding-left: 2em;
}

.accordion[class*='faq'] label::before {
	margin-left: -1em; 
	content: 'Q.';
}

.accordion[class*='faq'] .accordion-content > * {
	padding-left: 2em;
}

.accordion[class*='faq'] .accordion-content > *::before {
	margin-left: -1em; 
	content: 'A.';
}

.modal input {
	position: absolute;
	z-index: -1;
	opacity: 0;
}

.modal label {
	display: block; 
	cursor: pointer;
}

.modal .modal-overlay {
	opacity: 0; 
	-webkit-transition: .3s;
	        transition: .3s;
}

.modal .modal-window {
	position: fixed;
	top: 0;
	left: 0; 
	z-index: 9999;
	display: none;
}
.modal .modal-window .icon-right-top {
	position: absolute;
	top: 0;
	left: 100%;
	padding: 8px;
	width: 2em;
	height: 2em;
	border-radius: 50%;
	background-color: #72cfc7;
	text-align: center;
	color: #ffffff;
	line-height: 1em;
	-webkit-transition: .2s;
	        transition: .2s; 
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}
.modal .modal-window .icon-right-top::after {
	content: '\f00d'; 
	font-weight: 900;
	font-family: 'Font Awesome 5 Free';
}
.modal .modal-window .icon-right-top:hover,
.modal .modal-window .icon-right-top:active {
	background-color: #cf727a;
	-webkit-transform: translate(-50%, -50%) rotate(360deg);
	        transform: translate(-50%, -50%) rotate(360deg);
}
.modal .modal-window .modal-content {
	position: fixed;
	top: 50%;
	left: 50%;
	z-index: 9999;
	max-width: 600px; 
	max-height: 60vh;
	width: 80%;
	background-color: #ffffff;
}

.modal input:checked ~ .modal-window {
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display:         flex;
}
.modal input:checked ~ .modal-window .modal-content {
	-webkit-animation: modal-down ease-in-out	.4s forwards;
	        animation: modal-down ease-in-out	.4s forwards;
}

.modal input:checked ~ .modal-overlay {
	position: fixed;
	top: 0;
	left: 0; 
	z-index: 9999;
	width: 100vw;
	height: 100vh;
	background: rgba(0, 0, 0, .6);
	opacity: 1;
}

@-webkit-keyframes modal-down {
	0% {
		opacity: 0;
		-webkit-transform: translate(-50%, -100%);
		        transform: translate(-50%, -100%);
	}
	100% {
		opacity: 1;
		-webkit-transform: translate(-50%, -60%);
		        transform: translate(-50%, -60%);
	}
}

@keyframes modal-down {
	0% {
		opacity: 0;
		-webkit-transform: translate(-50%, -100%);
		        transform: translate(-50%, -100%);
	}
	100% {
		opacity: 1;
		-webkit-transform: translate(-50%, -60%);
		        transform: translate(-50%, -60%);
	}
}

.css-tab .css-tab-content [id*='tab'] {
	display: none;
}
.css-tab .css-tab-content [id*='tab'] + * {
	display: none;
}
.css-tab .css-tab-content [id*='tab']:target + * {
	display: block;
}
.css-tab .css-tab-content [id*='tab']:target + * > * {
	-webkit-animation: tab-up ease-in-out	.6s forwards;
	        animation: tab-up ease-in-out	.6s forwards;
}

.tab .tab-button {
	opacity: .6;
	cursor: pointer;
	-webkit-transition: .3s;
	        transition: .3s;
}
.tab .tab-button[class*='active'],
.tab .tab-button:hover {
	opacity: 1;
}

.tab .tab-content {
	display: none;
}
.tab .tab-content[class*='active'] {
	display: block;
	-webkit-animation: tab-up ease-in-out	.6s forwards;
	        animation: tab-up ease-in-out	.6s forwards;
}

@-webkit-keyframes tab-up {
	0% {
		opacity: 0;
		-webkit-transform: translate(0%, 50%);
		        transform: translate(0%, 50%);
	}
	50% {
		-webkit-transform: translate(0%, 0%);
		        transform: translate(0%, 0%);
	}
	100% {
		opacity: 1;
		-webkit-transform: translate(0%, 0%);
		        transform: translate(0%, 0%);
	}
}

@keyframes tab-up {
	0% {
		opacity: 0;
		-webkit-transform: translate(0%, 50%);
		        transform: translate(0%, 50%);
	}
	50% {
		-webkit-transform: translate(0%, 0%);
		        transform: translate(0%, 0%);
	}
	100% {
		opacity: 1;
		-webkit-transform: translate(0%, 0%);
		        transform: translate(0%, 0%);
	}
}

